"use client";

import React, { useEffect, useRef, useState } from "react";

const SignLanguageRecognizer = () => {
  const videoRef = useRef<HTMLVideoElement>(null);
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const [recognizedText, setRecognizedText] = useState("");
  const [error, setError] = useState("");
  const [isRecognizing, setIsRecognizing] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [cameraReady, setCameraReady] = useState(false);
  const [sessionLogs, setSessionLogs] = useState<Array<{timestamp: string, text: string}>>([]);
  const [isExporting, setIsExporting] = useState(false);
  const captureIntervalRef = useRef<NodeJS.Timeout | null>(null);
  const streamRef = useRef<MediaStream | null>(null);

  // Start camera stream
  const startCamera = async () => {
    try {
      const stream = await navigator.mediaDevices.getUserMedia({ 
        video: { 
          width: { ideal: 640 },
          height: { ideal: 480 },
          facingMode: "user"
        } 
      });
      
      if (videoRef.current) {
        videoRef.current.srcObject = stream;
        streamRef.current = stream;
        setCameraReady(true);
      }
    } catch (err: any) {
      setError("Camera access denied or unavailable. Please allow camera access and try again.");
      setCameraReady(false);
    }
  };

  // Stop camera stream
  const stopCamera = () => {
    if (streamRef.current) {
      streamRef.current.getTracks().forEach((track) => track.stop());
      streamRef.current = null;
    }
    if (videoRef.current) {
      videoRef.current.srcObject = null;
    }
    setCameraReady(false);
  };

  // Capture frame and send to API
  const captureAndRecognize = async () => {
    if (!videoRef.current || !canvasRef.current || isLoading) return;

    try {
      setIsLoading(true);
      setError("");
      
      const video = videoRef.current;
      const canvas = canvasRef.current;
      const context = canvas.getContext("2d");
      
      if (!context) {
        setError("Canvas context not available");
        return;
      }

      // Set canvas dimensions to match video
      canvas.width = video.videoWidth || 640;
      canvas.height = video.videoHeight || 480;

      // Draw current frame on canvas
      context.drawImage(video, 0, 0, canvas.width, canvas.height);

      // Convert canvas to base64 image (JPEG with quality 0.8)
      const imageData = canvas.toDataURL("image/jpeg", 0.8);

      // Call the recognition API
      const response = await fetch('/api/recognize', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ imageData })
      });

      const data = await response.json();
      
      if (response.ok) {
        const text = data.recognizedText;
        if (text && text !== 'No sign detected' && text !== 'No text recognized') {
          setRecognizedText(text);

          // Add to session logs
          setSessionLogs(prev => [...prev, {
            timestamp: new Date().toISOString(),
            text: text
          }]);

          // Speak the recognized text using Web Speech API
          if ("speechSynthesis" in window) {
            const utterance = new SpeechSynthesisUtterance(text);
            utterance.rate = 0.9;
            utterance.pitch = 1;
            utterance.volume = 1;
            window.speechSynthesis.speak(utterance);
          }
        }
      } else {
        setError(data.error || "Recognition failed. Please try again.");
      }
    } catch (e: any) {
      setError(e.message || "Error during recognition. Please check your connection.");
    } finally {
      setIsLoading(false);
    }
  };

  // Start recognition interval
  const startRecognition = () => {
    setIsRecognizing(true);
    setError("");
    // Capture every 3 seconds to avoid overwhelming the API
    captureIntervalRef.current = setInterval(captureAndRecognize, 3000);
  };

  // Stop recognition interval
  const stopRecognition = () => {
    setIsRecognizing(false);
    if (captureIntervalRef.current) {
      clearInterval(captureIntervalRef.current);
      captureIntervalRef.current = null;
    }
  };

  // Download session data as pickle file
  const downloadSession = async () => {
    if (sessionLogs.length === 0) {
      setError("No session data to export. Start recognition first.");
      return;
    }

    try {
      setIsExporting(true);
      setError("");

      const sessionData = {
        sessionId: Date.now().toString(),
        startTime: sessionLogs[0]?.timestamp || new Date().toISOString(),
        endTime: new Date().toISOString(),
        totalRecognitions: sessionLogs.length,
        recognizedTexts: sessionLogs,
        lastRecognizedText: recognizedText,
        metadata: {
          userAgent: navigator.userAgent,
          timestamp: new Date().toISOString(),
          appVersion: "1.0.0"
        }
      };

      const response = await fetch('/api/export', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(sessionData)
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || 'Failed to export session');
      }

      // Create blob and trigger download
      const blob = await response.blob();
      const url = window.URL.createObjectURL(blob);
      const a = document.createElement('a');
      a.href = url;
      a.download = `session_data_${Date.now()}.pkl`;
      document.body.appendChild(a);
      a.click();
      a.remove();
      window.URL.revokeObjectURL(url);

    } catch (e: any) {
      setError(e.message || 'Error during session export');
    } finally {
      setIsExporting(false);
    }
  };

  // Clear session logs
  const clearSession = () => {
    setSessionLogs([]);
    setRecognizedText("");
    setError("");
  };

  // Initialize camera on mount
  useEffect(() => {
    startCamera();
    return () => {
      stopRecognition();
      stopCamera();
    };
  }, []);

  return (
    <div className="flex flex-col items-center gap-6 p-6 max-w-4xl mx-auto">
      <div className="text-center">
        <h2 className="text-3xl font-bold text-gray-900 dark:text-white mb-2">
          Real-time Sign Language Recognition
        </h2>
        <p className="text-gray-600 dark:text-gray-300">
          Supports ASL, BSL, and ISL recognition with voice output
        </p>
      </div>

      <div className="relative w-full max-w-2xl border-2 border-gray-300 dark:border-gray-600 rounded-lg overflow-hidden bg-black">
        <video
          ref={videoRef}
          autoPlay
          playsInline
          muted
          className="w-full h-auto object-cover"
          onLoadedMetadata={() => setCameraReady(true)}
        />
        <canvas ref={canvasRef} style={{ display: "none" }} />
        
        {isLoading && (
          <div className="absolute inset-0 bg-black bg-opacity-50 flex items-center justify-center">
            <div className="text-white text-lg">Recognizing...</div>
          </div>
        )}
      </div>

      <div className="w-full max-w-2xl">
        <div className="bg-gray-100 dark:bg-gray-800 rounded-lg p-4 min-h-[60px]">
          <p className="text-center text-lg font-medium text-gray-900 dark:text-white">
            {recognizedText ? (
              <>
                <span className="text-sm text-gray-600 dark:text-gray-400">Recognized: </span>
                {recognizedText}
              </>
            ) : (
              <span className="text-gray-500 dark:text-gray-400">
                Position your hands in front of the camera to begin recognition
              </span>
            )}
          </p>
        </div>
        
        {error && (
          <div className="mt-4 p-3 bg-red-100 dark:bg-red-900 border border-red-400 dark:border-red-600 rounded-md">
            <p className="text-red-700 dark:text-red-200 text-center">{error}</p>
          </div>
        )}
      </div>

      <div className="flex flex-wrap gap-4 justify-center">
        {!isRecognizing ? (
          <button
            onClick={startRecognition}
            disabled={!cameraReady}
            className="px-6 py-3 bg-green-600 text-white rounded-lg hover:bg-green-700 disabled:bg-gray-400 disabled:cursor-not-allowed transition-colors"
          >
            Start Recognition
          </button>
        ) : (
          <button
            onClick={stopRecognition}
            className="px-6 py-3 bg-red-600 text-white rounded-lg hover:bg-red-700 transition-colors"
          >
            Stop Recognition
          </button>
        )}
        
        <button
          onClick={downloadSession}
          disabled={sessionLogs.length === 0 || isExporting}
          className="px-6 py-3 bg-blue-600 text-white rounded-lg hover:bg-blue-700 disabled:bg-gray-400 disabled:cursor-not-allowed transition-colors"
        >
          {isExporting ? 'Exporting...' : 'Download Session'}
        </button>
        
        <button
          onClick={clearSession}
          disabled={sessionLogs.length === 0}
          className="px-6 py-3 bg-gray-600 text-white rounded-lg hover:bg-gray-700 disabled:bg-gray-400 disabled:cursor-not-allowed transition-colors"
        >
          Clear Session
        </button>
      </div>

      {sessionLogs.length > 0 && (
        <div className="w-full max-w-2xl">
          <div className="bg-gray-50 dark:bg-gray-900 rounded-lg p-4">
            <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-2">
              Session Summary
            </h3>
            <p className="text-sm text-gray-600 dark:text-gray-400 mb-2">
              Total recognitions: {sessionLogs.length}
            </p>
            <div className="max-h-32 overflow-y-auto">
              {sessionLogs.slice(-5).map((log, index) => (
                <div key={index} className="text-xs text-gray-500 dark:text-gray-500 mb-1">
                  {new Date(log.timestamp).toLocaleTimeString()}: {log.text}
                </div>
              ))}
              {sessionLogs.length > 5 && (
                <div className="text-xs text-gray-400 dark:text-gray-600">
                  ... and {sessionLogs.length - 5} more
                </div>
              )}
            </div>
          </div>
        </div>
      )}

      <div className="text-sm text-gray-600 dark:text-gray-400 text-center max-w-md">
        <p>
          <strong>Tips:</strong> Ensure good lighting, keep hands clearly visible, 
          and make distinct gestures for best recognition results.
        </p>
      </div>
    </div>
  );
};

export default SignLanguageRecognizer;
